@import "../variable";

button {
  outline-color: rgba(255, 255, 255, 0) !important;
  outline: 0px solid white !important;
  -moz-outline: 0px solid white !important;
  -webkit-outline: 0px solid white !important;
}

button + button {
  //fixing top btn
  //margin-left: 3px;
}

#cancel-button {
  margin-left: 3px;
}

// B-SWITCH
.bootstrap-switch {
  //border: 2px solid #dbdfef !important;
  //border-radius: 5px;
  border: 2px solid $border_color;
  .bootstrap-switch-handle-on.bootstrap-switch-success,
  .bootstrap-switch-handle-off.bootstrap-switch-success {
    background: $green;
    border-color: $green;
  }
}

// B-TAGS-INPUT
.bootstrap-tagsinput {
  width: 100%;
}

.btn{
  table & ,&.popup-extension{
    padding: 8px 13px !important;
  }
}
.panel-default {
  .btn{
    &:not(.note-btn){
      padding: 8px 30px;
    }
    border-radius: 4px;
    font-size: 15px;
    &-default {
      color: #fff;
      background: #cfcfcf;
      border: 2px solid #b1b1b1;
      &.active,&:focus,&:hover{
        background: #b1b1b1;
        border-color:#9d9d9d;
      }
    }
    &-info{
      color: #000;
      background: $info-color;
      border: $info-border;
      &.active,&:focus,&:hover{
        background: $info-hover-color;
        border-color:#acbcd9;
      }
    }
    &-danger {
      color: #fff;
      background: #cf4949;
      border: 2px solid #b33131;
    }
    &-primary {
      box-shadow: none;
      background: #4fb5e7;
      border: 2px solid #3aa5da;
      color: #fff;
      &:focus,&:hover{
        background: #27b7fd;
        border-color:#229cd8;
      }
      &:active:focus{
        background: #1589c1;
        border-color:#0871a5;
    }
    }
  }
}
.input-group-addon:last-child{
  border-bottom-left-radius:0;
  border-top-left-radius:0;
}
